import { Component } from '@angular/core';
import { NavController,AlertController,Platform } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { RegisterPage } from '../register/register';
import { RegisterPageMRI } from '../register_mri/register_mri';
import { TestPage } from '../test/test';
import { Toast } from '../toast/toast';
import { TwoFactorState } from '../twofatorstate/twofatorstate';
import * as Constants from '../twofatorstate/constants';

declare var com: any;
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController, public toast : Toast,public alertCtrl: AlertController,public platform:Platform) {
    
    document.addEventListener('onInitializeCompleted',  (e: any)=> {
      //console.log(e);
      alert('onInitializeCompleted');
      let res = JSON.parse(e.response);
      //console.log("error code--->" + responseJson.errCode);

      if (res.errCode == 0) {
        //this.toast.showToast("Initialization Done");

        //  var statusCode = res.pArgs.response.StatusCode;
        //   if (statusCode == 100) {
          let challengeJson;
        if (res.pArgs.response.ResponseData) {
          challengeJson = res.pArgs.response.ResponseData;
          TwoFactorState.initialChallengesJson = challengeJson;
        }

        
      }
      else
        this.toast.showToast("Initialization Failed");

      //alert(JSON.stringify(e));
    });
   
    setTimeout(() => {
      this.initRelID();
     }, 2000);

     document.addEventListener('onSecurityThreat',  (e: any)=> {
      //alert(JSON.parse(JSON.stringify(e)).response);
      //platform.exitApp();
      //ionic.Platform.exitApp();
      let confirmAlert = this.alertCtrl.create({
        title: 'Threat detected',
        message: JSON.parse(JSON.stringify(e)).response,
        buttons: [{
          text: 'OK',
          role: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
            //confirmAlert.
            if(this.platform.is('android'))
            this.platform.exitApp();
          }
        }]
      });
      confirmAlert.present();
    });
     
  }

  login() {
    this.navCtrl.push(LoginPage);
  }
  register() {
    if(Constants.IS_MRI == 0)
    this.navCtrl.push(RegisterPage);
    else
    this.navCtrl.push(RegisterPageMRI);
  }

  test() {
    this.navCtrl.push(TestPage);
  }
  
  initRelID() {
   // this.toast.showLoader();
   if(Constants.IS_MRI == 1)
   com.uniken.rdnaplugin.RdnaClient.initialize(this.initSuccess, this.initFailure, ["", "18.232.236.255", "4443", "AES/256/CFB/PKCS7Padding", "Rel-ID-Secure-IV", "",com.uniken.rdnaplugin.RdnaClient.RDNALoggingLevel.RDNA_NO_LOGS]);
   else{
    com.uniken.rdnaplugin.RdnaClient.setApplicationVersion(this.initSuccessVersionName, this.initFailureVersionName,["6.0.1"]);
    com.uniken.rdnaplugin.RdnaClient.initialize(this.initSuccess, this.initFailure, ["", "poc7-uniken.com", "4443", "AES/256/CFB/PKCS7Padding", "Rel-ID-Secure-IV", ""]); 
  }
   //com.uniken.rdnaplugin.RdnaClient.initialize(this.initSuccess, this.initFailure, ["", "poc7-uniken.com", "4443", "AES/256/CFB/PKCS7Padding", "Rel-ID-Secure-IV", ""]);
   //com.uniken.rdnaplugin.RdnaClient.initialize(this.initSuccess, this.initFailure, ["", "10.0.5.21", "4443", "AES/256/CFB/PKCS7Padding", "Rel-ID-Secure-IV", ""]);
   
  }


  initSuccess(data) {
   // this.toast.hideLoader();
    console.log("RdnaClient.js: initSuccess");
  }

  initFailure(data) {
    //this.toast.hideLoader();
    this.toast.hideLoader();
    console.log("RdnaClient.js: initFailure");
  }

  initSuccessVersionName(data) {
    // this.toast.hideLoader();
    alert("initSuccessVersionName");
     console.log("RdnaClient.js: initSuccessVersionName");
     //com.uniken.rdnaplugin.RdnaClient.initialize(this.initSuccess, this.initFailure, ["", "poc7-uniken.com", "4443", "AES/256/CFB/PKCS7Padding", "Rel-ID-Secure-IV", ""]);
   }
 
   initFailureVersionName(data) {
     //this.toast.hideLoader();
     //his.toast.hideLoader();
     alert("initFailureVersionName");
     console.log("RdnaClient.js: initFailureVersionName");
   }

}



